<template>
  <div ref="scroll-wrpper" class="wrapper">
    <div ref="scroller" class="scroller">
      <slot></slot>
    </div>
  </div>
</template>

<script>
import AlloyTouch from "alloytouch"
import Transform from "alloytouch/transformjs"
export default {
  name: 'NaviScroll',
  props: {
    touch: {
      type: [String, HTMLDivElement],
      default: '.wrapper'
    },
    vertical: {
      type: Boolean,
      default: true
    },
    property: {
      type: String,
      default: 'translateY'
    },
    min: {
      type: Number,
      default: 0
    },
    max: {
      type: Number,
      default: 0
    },
    step: {
      type: Number,
      default: 0
    },
    inertia: {
      type: Boolean,
      default: true
    },
    touchEndReturn: {
      type: [String, Boolean],
      default: ''
    },
    // spring: {
    //   type: Boolean,
    //   default: true
    // },
    initialValue: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      target: null,
      alloyTouch: null
    }
  },
  mounted() {
    setTimeout(() => {
      this.init()
    }, 20);
  },
  watch: {
    min (val) {
      if (this.alloyTouch && val) {
        this.alloyTouch.min = val
      }
    }
  },
  methods: {
    init() {
      let _this = this
      // let target = document.querySelector("#scroller");
      this.target = this.$refs.scroller
      //elementtransform
      Transform(this.target, true);
      this.alloyTouch = new AlloyTouch({
        touch: this.touch, //dom
        vertical: this.vertical, //truetouch
        target: this.target, //
        property: this.property, //
        sensitivity: 1, //,1
        factor: 1, //,1touch1pxtarget.y1
        min: this.min, //,
        max: this.max, //,
        step: this.step === 0 ? undefined : this.step,
        // spring: this.spring, //,true
        inertia: this.inertia,
        maxSpeed: 2,
        // initialValue: 0,
        animationEnd: function(value) {
          _this.$emit('animationEnd', value)
        },
        pressMove: function(evt, value) {
          //console.log(evt.deltaX + "_" + evt.deltaY + "__" + value);
          _this.$emit('pressMove', evt, value)
        },
        change: function (value) {
          _this.$emit('change', value)
        },
        touchStart: function (evt, value) {
          _this.$emit('touchStart', evt, value)
        },
        touchMove: function (evt, value) {
          _this.$emit('touchMove', evt, value)
        },
        touchEnd: function (evt, value, index) {
          _this.$emit('touchEnd', evt, value, index)
          return typeof _this.touchEndReturn === 'boolean' ? _this.touchEndReturn : undefined
        },
        tap: function (evt, value) {
          _this.$emit('tap', evt ,value)
        }
      })
      this.$emit('initSuccess')
    },
    to (value, time, ease) {
      this.alloyTouch.to(value, time, ease)
    },
    stop () {
      this.alloyTouch.stop()
    }
  }
}
</script>

